import {
	Table,
	TableBody,
	TableRow,
	TableContainer,
	TableCell,
} from '@material-ui/core';
import _get from 'lodash.get';
import TextRow from '../../../components/TextRow';
import { TProperty } from '../../../../../types';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { PropertyExternalUrl } from './PropertyExternalUrl';
import { convertToFile } from '../../../../../helpers';
import Preview from '../../../components/UploadFile/Preview';

type Props = {
	property: TProperty;
};

export const ListingInformation = ({ property }: Props) => {
	const externalId = _get(property, 'ExternalId', '');
	const statusId = +_get(property, 'StatusId', 0);
	const thumbnail = _get(property, 'Thumbnail', '');

	return (
		<SubCard title='Listing Information'>
			<TableContainer>
				<Table
					sx={{
						'& td': {
							borderBottom: 'none',
						},
					}}
					size='small'
				>
					<TableBody>
						<TextRow<TProperty>
							data={property}
							source='ListPriceRange.Min'
							label='Listing Price Min'
						/>
						<TextRow<TProperty>
							data={property}
							source='ListPriceRange.Max'
							label='Listing Price Max'
						/>
						<TextRow<TProperty>
							data={property}
							source='BuyerAgentFeePct'
							label='Buyer Agent Fee'
						/>
						<TextRow<TProperty>
							data={property}
							source='ListingAgentFeePct'
							label='Listing Agent Fee'
						/>
						<TextRow<TProperty>
							data={property}
							source='ListingTerminationDate'
							label='Listing Termination Date'
						/>
						<TextRow<TProperty>
							data={property}
							source='ListingTermsConditions'
							label='Special Listing Terms & Conditions'
						/>
						<TextRow<TProperty>
							data={property}
							source='AdditionalInfo'
							label='Property Description'
						/>
						<TableRow>
							<TableCell variant='head' sx={{ width: { sm: 220 }, padding: 0 }}>
								Photo
							</TableCell>
							<TableCell>
								{Boolean(thumbnail) && <Preview file={convertToFile(thumbnail)} />}
							</TableCell>
						</TableRow>
						<TableRow>
							<TableCell variant='head' sx={{ width: { sm: 220 }, padding: 0 }}>
								Listed Property URL
							</TableCell>
							<TableCell>
								<PropertyExternalUrl externalId={externalId} status={statusId} />
							</TableCell>
						</TableRow>
					</TableBody>
				</Table>
			</TableContainer>
		</SubCard>
	);
};
